body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  
}
#image-cover { 
 position: absolute;
 z-index: -1;
 width: 100%;
 height:100%;
 -webkit-filter: blur(5px);
 filter: blur(5px);
}
a:hover{
  text-decoration: none;
}
footer {
  color: white;
  min-height: 100%;  
  left:0px;
  bottom:0px;
  height:30px;
  width:100%;
  background:rgb(0, 0, 0);
  margin-bottom: 0px;
}
#root{
  background: -webkit-gradient(linear, right top, left top, from(#bdc3c7), to(#2c3e50));
  background: -webkit-linear-gradient(right, #bdc3c7, #2c3e50);
  background: -o-linear-gradient(right, #bdc3c7, #2c3e50);
  background: linear-gradient(-90deg, #bdc3c7, #2c3e50);
}
h1, h2, h3 {
  font-family: 'Bungee', cursive;
}





